import { Component } from '@angular/core';
import { TmdbService } from '../services/tmdb.service';
import { Film } from '../interfaces/interfaces';
import { ModalController } from '@ionic/angular';
import { DetailComponent } from '../components/detail/detail.component';
import { Router } from '@angular/router';

@Component({
  selector: 'app-tab2',
  templateUrl: 'tab2.page.html',
  styleUrls: ['tab2.page.scss']
})
export class Tab2Page {

  logoUrl: string = "../../assets/logo.svg";

  sugerencias: String[] = ["Spiderman", "La vida es bella", "Star Wars", "Harry Potter", "Vengadores"]

  searchText: string;

  results: Film[] = [];

  searching: boolean = false;

  noResults: boolean = false;

  type: string = '';

  constructor(private tmdb: TmdbService,
                private modalCtrl: ModalController,
                private router: Router) {}

  search(event): void{
    this.searching = true;
    const value: string = event.detail.value;
    this.searchText = value;
    this.results = [];
    if(value.length == 0){
      this.searching = false;
      this.noResults = false;
      return;
    }
    this.tmdb.searchFilms(value).subscribe( res => {
      this.results = [...this.results, ...res.results];
      this.results = this.results.filter(this.filmfilter);
      console.log(this.results)
      if(this.results.length == 0) {
        this.noResults = true;
      } else {
        this.noResults = false;
      }
      this.searching = false;
    }, err => {
      this.searching = false;
      this.noResults = true;
    })
    this.tmdb.searchSerie(value).subscribe( res => {
      this.results = [...this.results, ...res.results];
      this.results = this.results.filter(this.filmfilter);
      if(this.results.length == 0) {
        this.noResults = true;
      } else {
        this.noResults = false;
      }
      this.searching = false;
    }, err => {
      this.searching = false;
      this.noResults = true;
    })
  }

  async showDetails(id: string, title: string, name: string): Promise<void>{
    let style = '';
    let _name = ''
    if(name != undefined){
      style = 'tv'
      _name = name
    }
    if(title != undefined){
      style = 'movie'
      _name = title
    }
    this.router.navigate([`/app/details/${id}/${style}/${_name}`])
  }

  filmfilter(film: Film): boolean {
    if(film.poster_path){
      return true
    } else {
      return false
    }
  }
}
